*{
  margin: 5px;
}

.App{
  display: flex;
  flex-direction: column;
}

.App-header{
  color: red;
  border-bottom: 3px solid red;
  float: left;
  padding: .5em 1em;
  border-width: 100%;
}

.App-header::after {
  content: "";
  display: table;
  clear: both;
}

.App-header h1{
  font-size: 1.5em;
  margin: 2.0em 0 .3em 0;
  display: inline-block;
}

.App-header img{
  max-width: 120px;
  float: left;
  margin: 0 10px 0 0;
}

.App-body{
  padding: 35px 0px 0px 35px;
  height: 50vh;
}

.App-footer{
  text-align: center;
  border-top: 3px solid red;
  padding-top: 10px;
  font-style: italic;
}